<template>
  <list
  :list="list"
  :total="total"
  :loading="loading"
  :page="page"
  @click="toPage"
  />
</template>

<script>
import formatDate from '@/tools/format-date';

export default {
  props: {
    title: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      results: [],
      total: 0,
      page: 1,
      loading: true,
    };
  },
  computed: {
    list() {
      return this.results.map(obj => ({
        content: `${obj.title} - ${obj.username} - ${formatDate(obj.createdAt)}`,
        // eslint-disable-next-line
        to: `/results/${obj._id}`,
      }));
    },
  },
  mounted() {
    this.getData();
  },
  methods: {
    toPage(page) {
      this.page = page;
      this.getData();
    },
    getData() {
      this.$axios.get(`${this.$url.results}?title=${this.title}&page=${this.page}`)
        .then((resp) => {
          this.results = resp.data.results;
          this.total = resp.data.total;
          this.loading = false;
        })
        .catch((err) => {
          this.$alert('error', err.message);
          this.loading = false;
        });
    },
  },
};
</script>
