export default function (timeStamp) {
  const date = new Date(timeStamp);
  const year = date.getFullYear();
  let month = date.getMonth() + 1;
  let day = date.getDay();
  let hour = date.getHours();
  let min = date.getMinutes();
  if (month < 10) {
    month = '0' + month;
  }
  if (day < 10) {
    day = '0' + day;
  }
  if (hour < 10) {
    hour = '0'+ hour;
  }
  if(min < 10) {
    min = '0' + min;
  }
  return `${year}-${month}-${day} ${hour}:${min}`;
}
