<template>
<div class="flex-row pd-1 settings">
  <form class="w-50 flex-column pure-form pure-form-stacked settings-form">
    <div class="form-group">
      <span>访问限制</span>
      <label for="public" class="pure-radio">
        <input type="radio" id="public" name="accessbility" :value=0 v-model="settings.accessbility">
        公开
      </label>
      <label for="private" class="pure-radio">
        <input type="radio" id="private" name="accessbility" :value=1 v-model="settings.accessbility">
        私有
      </label>
      <span v-if="settings.accessbility === 0" class="pure-form-message">
        允许任何人访问
      </span>
      <span v-else class="pure-form-message">
        只允许指定用户访问
      </span>
    </div>
    <div class="form-group">
      <span>时间限制</span>
      <input type="number" v-model="settings.timeLimit.minutes">
      <span class="pure-form-message">
        分
      </span>
      <input type="number" v-model="settings.timeLimit.seconds">
      <span class="pure-form-message">
        秒
      </span>
    </div>
    <div class="form-group">
      <span>计时方式</span>
      <label for="timerMode1">
        <input type="radio" name="timerMode" id="timerMode1" v-model="settings.timerMode" :value=0>
        单个问题
      </label>
      <label for="timerMode2">
        <input type="radio" name="timerMode" id="timerMode2" v-model="settings.timerMode" :value=1>
        所有问题
      </label>
      <span v-if="settings.timerMode === 0" class="pure-form-message">
        在限定时间内回答一个问题
      </span>
      <span v-else class="pure-form-message">
        在限定时间内回答所有问题
      </span>
    </div>
    <div class="form-group">
      <span>开放时间</span>
      <label for="schedule-on">
        <input type="radio" name="schedule" id="schedule-on" v-model="settings.schedule" :value=0>
        不限制
      </label>
      <label for="schedule-off">
        <input type="radio" name="schedule" id="schedule-off" v-model="settings.schedule" :value=1>
        在特定时间开放
      </label>
      <span v-if="settings.schedule === 0" class="pure-form-message">
        测试可以在任何时间进行
      </span>
      <span v-else class="pure-form-message">
        测试只能在指定时间进行
      </span>
    </div>
    <div v-if="settings.schedule" class="form-group">
      <span class="form-group-label">测试开放时间</span>
      <div class="pure-g">
        <input type="date"  id="scheduleDate" required v-model="settings.date">
        <input type="time"  id="scheduleTime" required v-model="settings.time">
      </div>
    </div>
    <div class="ml-auto mt-auto flex-row">
      <button @click.prevent="reset" class="pure-button" :disabled="flag">Reset</button>
      <button class="ml-1 pure-button pure-button-primary" :disabled="flag" @click.prevent="save">
        Save
      </button>
      <button class="ml-1 pure-button button-error" :disabled="flag" @click.prevent="del">
        Delete
      </button>
    </div>
  </form>
  <whitelist 
  class="flex-1"
  v-if="settings.accessbility === 1"
  :list="settings.whitelist"
  @add-mail="toAddMail"
  @del-mail="toDelMail"
  /> 
</div>
</template>

<script>
import Whitelist from './components/white-list.vue';

export default {
  components: {
    Whitelist,
  },
  props: {
    title: {
      type: String,
    },
  },

  data() {
    return {
      settings: {
        accessbility: 0,
        timeLimit: {
          minutes: 0,
          seconds: 10,
        },
        timerMode: 0,
        whitelist: [],
        schedule: 0,
        scheduleAt: 0,
        date: 0,
        time: 0,

      },
      flag: false,
    };
  },
  mounted() {
    let settings;
    if (this.title) {
      settings = JSON.parse(localStorage.getItem(`${this.title}-settings`));
    }
    if (settings) {
      this.settings = settings;
    }
  },
  methods: {
    toAddMail(mail) {
      if (this.settings.whitelist.indexOf(mail.toLowerCase()) === -1) {
        this.settings.whitelist.push(mail.toLowerCase());
      }
    },
    toDelMail(index) {
      this.settings.whitelist.splice(index, 1);
    },
    save() {
      if (this.flag) {
        return;
      }
      this.flag = true;
      if (this.settings.schedule) {
        const date = document.getElementById('scheduleDate');
        const time = document.getElementById('scheduleTime');
        if (date.checkValidity() && time.checkValidity()) {
          const dateTime = `${date.value} ${time.value}`;
          const timeStamp = new Date(dateTime).getTime();
          this.settings.scheduleAt = timeStamp;
        }
      }
      localStorage.setItem(`${this.title}-settings`, JSON.stringify(this.settings));
      this.$axios.put(`${this.$url.tests}/${this.title}`, { settings: this.settings })
        .then(() => {
          this.$alert('success', 'Saved');
          this.flag = false;
        })
        .catch(() => {
          this.flag = false;
        });
    },
    reset() {
      if (this.flag) {
        return;
      }
      this.flag = true;
      const settings = JSON.parse(localStorage.getItem(`${this.title}-settings`));
      if (settings) {
        this.settings = settings;
      }
      this.flag = false;
    },
    del() {
      if (this.flag) {
        return;
      }
      this.flag = true;
      this.$axios.delete(`${this.$url.tests}/${this.title}`)
        .then((resp) => {
          this.flag = false;
          if (resp.data.ok) {
            this.$alert('success', '删除成功!');
            setTimeout(() => {
              this.$router.replace('/');
            }, 300);
          }
        })
        .catch(() => {
          this.flag = false;
        })
    },
  },
};
</script>