<template>
  <div class="test">
    <div class="flex-row">
      <h1 class="test__title">{{ test.title }}</h1>
      <button class="ml-auto pure-button pure-button-primary flex-item-center">
        <a :href="'/testing/' + test.title" target="_blank">
          Start Test
        </a>
      </button>
    </div>
    <div class="flex-row">
      <ul class="meta-list">
        <li class="meta-list__item">
          <span class="meta-list__item-key">作者</span>
          <span class="meta-list__item-value">{{ test.author }}</span>
        </li>
        <li class="meta-list__item">
          <span class="meta-list__item-key">创建时间</span>
          <span class="meta-list__item-value">{{ test.createdAt | formatDateTime }}</span>
        </li>
        <li class="meta-list__item">
          <span class="meta-list__item-key">评分</span>
          <star
          class="meta-list__item-value"
          :disable="test.vote.disable"
          :count="6 - test.vote.count"
          @click="toVote"
          />
        </li>
        <li class="meta-list__item">
          <span class="meta-list__item-key">标签</span>
          <span class="meta-list__item-value tag-list">
            <tag
            class="mr-1 mb-1"
            v-for="tag in test.tags"
            :text="tag"
            :key="tag"
            />
          </span>
        </li>
      </ul>
      <rating 
      class="ml-auto"
      :data="test.rating"
      />
    </div>
    <p class="test__intro">{{ test.intro }}</p>
    <ot-comment 
    :data="test.comments"
    :flag="flag"
    @submit="toComment"
    />
  </div>
</template>

<script>
export default {
  props: {
    test: {
      type: Object,
      required: true,
    },
    flag: {
      type: Boolean,
      default: false,
    }
  },
  data() {
    return {
      vote: {
        disable: false,
        count: 0,
      },
    };
  },
  methods: {
    toComment(data) {
      this.$emit('comment', data);
    },
    toVote(count) {
      if (!window.$user) {
        this.alert('error', '請先登錄');
        return;
      }
      this.test.vote.disable = true;
      this.test.vote.count = count;
      this.$axios.put(`${this.$url.stars}/${this.test.title}`, { count })
        .then(console.log)
        .catch(console.log);
    },
  },
};
</script>

