<template>
  <div class="flex-row" v-loading="loading">
    <sidebar 
    v-if="isAuthor"
    :tabs="tabs"
    :active="active"
    @click="toClickTab"
    />
    <div class="flex-1 flex-column">
    <info
    class="flex-1"
    v-if="tabs[active].component === 'info' && !loading"
    :test="test"
    :flag="comment.flag"
    @comment="toComment"
    />
    <settings
    class="flex-1"
    v-else-if="tabs[active].component === 'settings' && !loading && isAuthor"
    :title="title"
    @save="toSave"
    />
    <submissions
    class="flex-1"
    v-else-if="tabs[active].component === 'submissions' && !loading && isAuthor"
    :title="title"
    />
    </div>
  </div>
</template>

<script>
import Settings from './Settings/index.vue';
import Info from './Info/index.vue';
import Submissions from './Submissions/index.vue';

export default {
  components: {
    Info,
    Settings,
    Submissions,
  },
  props: {
    title: {
      type: String,
      required: true,
    },
  },
  data() {
    return {
      tabs: [
        {
          name: '基础信息',
          component: 'info',
        },
        {
          name: '设置',
          component: 'settings',
        },
        {
          name: '测试记录',
          component: 'submissions',
        }
      ],
      active: 0,

      loading: true,
      isAuthor: false,

      test: {},

      comment: {
        flag: false,
      },
      settings: {
        flag: false,
      },
    };
  },
  mounted() {
    this.getData();
  },
  methods: {
    getData() {
      this.$axios.get(`${this.$url.tests}/${this.title}`)
      .then((resp) => {
        this.test = resp.data;
        this.loading = false;
        if (window.$user && (this.test.author === window.$user.username)) {
          this.isAuthor = true;
          window.localStorage.setItem(`${this.title}-settings`, JSON.stringify(this.test.settings));
        }
      })
      .catch((err) => {
        this.loading = false;
        this.$alert('error', err.response.data);
        this.$router.replace('/');
      });
    },
    toClickTab(index) {
      this.active = index;
    },
    toComment(data) {
      if (this.comment.flag) {
        return;
      }
      data.title = this.title;
      this.comment.flag = true;
      this.$axios.post(`${this.$url.tests}/${this.title}/comments`, data)
      .then((resp) => {
        this.test.comments.push(resp.data);
        this.comment.flag = false;
      })
      .catch(() => {
        this.comment.flag = false;
      });
    },
    toSave(settings) {
      if (this.settings.flag) {
        return;
      }
      this.$axios.put(`${this.$url.tests}/${this.title}`, { settings })
        .then(() => {
          this.$alert('success', 'Saved');
          this.settings.flag = false;
        })
        .catch(() => {
          this.settings.flag = false;
        });
    },
  },
  destroyed() {
    localStorage.removeItem(`${this.title}-settings`);
  }
};
</script>