<template>
  <div class="whitelist bd-1 shadow-1">
    <div class="flex-row pure-form whitelist-header">
      <input type="text" id="whitelist-search" placeholder="Search" v-model="keyword">
      <label for="whitelist-selectall" class="ml-auto">
        全选
        <input type="checkbox" id="whitelist-selectall" @change="selectAll">
      </label>
      <button class="pure-button pure-button-primary ml-1" @click.prevent="sendMail">发送邀请</button>
    </div>
    <div class="whitelist-body">
      <form class="mt-1 flex-row pure-form">
        <div class="pure-control-group ml-auto">
          <input type="email" placeholder="Email" id="email" required>
          <button @click.prevent="addEmail" class="pure-button ml-1">+ 添加</button>
        </div>
      </form>
      <ul class="mt-1">
        <li class="flex-row mt-1" v-for="(email, index) in filterdMails" :key="email">
          <label class="flex-1  flex-item-center ml-1" >
            <input type="checkbox" class="mail-checkbox">
            {{ email }}
          </label>
          <button class="pure-button button-error" @click.prevent="delEmail(index)">删除</button>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    list: {
      type: Array,
      default: [],
    }
  },
  computed: {
    filterdMails() {
      if (!this.keyword) {
        return this.list;
      }
      const reg = new RegExp(this.keyword);
      return this.list.filter(mail => reg.test(mail));
    },
  },
  data() {
    return {
      keyword: '',
    }
  },
  methods: {
    selectAll(event) {
      const el = event.target;
      if (el.checked) {
        const els = document.getElementsByClassName('mail-checkbox');
        for (let i = 0; i < els.length; i += 1) {
          els[i].checked = true;
        }
      } else {
        const els = document.getElementsByClassName('mail-checkbox');
        for (let i = 0; i < els.length; i += 1) {
          els[i].checked = false;
        }
      }
    },
    sendMail() {
      this.$axios.post(`${this.$url.sendmail}/turing%20test`)
        .then(() => {
          this.$alert('success', 'send mail success.');
        })
        .catch(console.log);
    },
    addEmail() {
      const email = document.getElementById('email');
      if (email.checkValidity()) {
        this.$emit('add-mail', email.value);
      } else {
        // hack
        const btn = document.createElement('button');
        email.appendChild(btn);
        btn.click();
        email.removeChild(btn);
      }
    },
    delEmail(index) {
      this.$emit('del-mail', index);
    },
  }
}
</script>

<style lang="scss" scoped>

.whitelist {
  padding: $pd-1;
  margin-left: 0.4em;
  overflow-y: auto;
  position: relative;
  height: 80vh;
  width: 30em;
}

.whitelist-header {
  background: #eee;
  * {
    align-self: center;
  }
}

.whitelist-body {
  li:hover {
    background: #ccc;
  }
}
</style>
